.filter-slider{
  &__item{
    min-height: 1.375em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 2;
  }
  .range-slider{
    width: 100%;
    background-color: #d8d8d8;
    border: none;
    box-shadow: none;
    height: .5em;
    border-radius: .125em;
    .noUi-connect{
      background-color: $primary;
    }
    .noUi-handle{
      box-shadow: none;
      border: none;
      background-color: $black;
      width: .375em;
      height: 1.375em;
      top: -.4375em !important;
      right: -.1875em !important;
      border-radius: .125em;
      cursor: pointer;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
  &__title{
    font-size: 1em;
    letter-spacing: 0.02em;
    font-family: $font2;
    padding-top: 1em;
    position: relative;
    white-space: nowrap;
    &:nth-child(1){
      &:before{
        display: none;
      }
    }
    &:nth-child(3){
      transform: translateX(.875em);
    }
    &:nth-child(4){
      transform: translateX(1.875em);
    }
    &:nth-child(5){
      transform: translateX(3.9375em);
    }
    &:last-child{
      transform: unset;
      &:before{
        left: unset;
        right: 0;
        transform: unset;
      }
    }
    &:before{
      content: '';
      position: absolute;
      bottom: 100%;
      left: 50%;
      transform: translateX(-50%);
      width: .375em;
      height: 1.375em;
      background: #d8d8d8;
      border-radius: .125em;
    }
  }
}


.filter-slider2{
  .filter-slider__title{
    &:nth-child(2){
      transform: translateX(-.5em);
    }
    &:nth-child(3){
      transform: translateX(0);
    }
    &:nth-child(4){
      transform: translateX(2.375em);
    }
  }
}
