.filter{
  display: flex;
  justify-content: space-between;
  margin-top: .5em;
  &__items{
    display: flex;
    flex-wrap: wrap;
  }
  &__left{
    display: flex;
  }
  .param-select{
    margin: .75em .75em 0 0;
  }
  &__clear{
    flex-shrink: 0;
    @include center;
    width: 3em;
    height: 3em;
    border-radius: 50%;
    box-shadow: 0 .25em .375em 0 rgba(0, 0, 0, 0.24);
    background: #202020;
    margin-right: .75em;
    margin-top: .25em;
    display: none;
    svg{
      width: 1em;
    }
    &:hover{
      background-color: $primary;
      path{
        stroke: $black;
      }
    }
  }
  &._filters-selected{
    .filter__clear{
      display: flex;
    }
  }
}

.param-select2,
.param-select3{
  .param-select__show{
    svg{
      margin-left: .75em;
    }
  }
  &._open{
    .param-select__show{
      border-color: $primary;
      background-color: #fff;
    }
  }
}

.param-select2{
  .param-select__hidden{
    width: auto;
    min-width: calc(100% + .75em);
    left: -.375em;
    top: -.375em;
    padding: 3.125em .375em .75em;
    overflow-x: hidden;
  }
  .param-select__items{
    padding-right: 0;
  }
  .param-select__option{
    display: inline-flex;
    margin-right: auto;
    label{
      min-height: unset;
      display: flex;
      align-items: center;
      padding: .375em .875em;
      font-size: 1em;
      line-height: 119%;
      font-weight: 600;
      border-radius: .625em;
      border: 1px solid $primary;
      &:before{
        display: none;
      }
    }
    input:checked{
      &~label{
        background-color: $primary;
        padding-left: 2.5em;
      }
      &~.param-select__option-clear{
        display: flex;
      }
    }
  }
  .param-select__clear,
  .param-select__option-clear{
    display: none;
    align-items: center;
    justify-content: center;
    width: .875em;
    position: absolute;
    top: .5625em;
    left: .875em;
    z-index: 3;
    img{
      width: 100%;
      display: block;
    }
  }
  &._selected{
    .param-select__show{
      padding-left: 2.5em;
      background-color: $primary;
      path{
        fill: $black;
      }
    }
    .param-select__clear{
      display: flex;
    }
  }
}

.param-select3{
  .param-select__hidden{
    top: 100%;
    margin-top: .75em;
    width: 17.125em;
    padding: .625em .5em;
  }
  .search-field{
    input{
      height: 2.875em;
    }
  }
  .param-select__items{
    gap: .375em;
    max-height: 20em;
    margin-top: .375em;
  }
  .param-select__option{
    display: inline-flex;
    margin-right: auto;
    label{
      min-height: unset;
      display: flex;
      align-items: center;
      padding: .43em .57em;
      font-size: .875em;
      line-height: 136%;
      border-radius: 3.57em;
      font-family: $font2;
      &:before{
        display: none;
      }
    }
    input:checked{
      &~label{
        background-color: #F0F5D2;
      }
    }
  }
  .param-select__nav{
    padding: .75em .75em 0;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .375em));
    gap: .75em;
  }
  .param-select__btn{
    height: 2.375em;
    width: 100%;
    padding: 0;
    span{
      font-size: .875em;
    }
  }
}

.sort{
  .param-select__show{
    padding: 0;
    border: none;
    svg{
      margin-left: .75em;
    }
  }
  .param-select__hidden{
    width: auto;
    min-width: 100%;
    white-space: nowrap;
    top: 100%;
    right: 0;
    left: unset;
    padding: .75em;
  }
}

.param-slider{
  @media screen and (min-width: 993px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        flex-wrap: wrap;
      }
      &-slide{
        width: auto;
        padding: 0;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .filter{
    display: block;
    margin: 0;
    &__right{
      margin-top: 1.375em;
      display: flex;
    }
    .param-select{
      margin: 0;
    }
    &__clear{
      margin-top: -.5em;
      position: relative;
      z-index: 3;
    }
  }

  .sort{
    .param-select__hidden{
      right: unset;
      left: 0;
    }
    .param-select{
      margin: 0;
    }
  }

  .param-slider{
    .swiper{
      &-slide{
        width: auto;
        padding-right: .75em;
        margin: 0;
      }
    }
  }

  .masters-page{
    .filter{
      margin-top: 1.25em;
    }
  }
}


